body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Gothic A1', sans-serif;
  font-weight: 300;
}

.btn-outline-light{
  padding: 0.938rem 2rem;
  text-align: center;
  text-decoration: none;
  border-radius: 0.625rem;
  display: inline-block;
  font-size: 0.813rem;
  margin: 0.625rem;
}

.bi-info-circle{
  display: flex;
  justify-content: right;
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  transition: 0.2s ease-in-out;
}

.bi-info-circle:hover{
  fill-opacity: 0.3;
}

h1{
  font-size: calc(7.5rem + 2vmin);
}

@-webkit-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.App-header {
  background-image: linear-gradient(to top, #249494 0%, #330867 100%);
  background-size: 180% 180%;
  -webkit-animation: gradient-animation 18s ease infinite;
          animation: gradient-animation 18s ease infinite;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(0.938rem + 2vmin);
}

.App-body {
  font-size: calc(0.625rem + 2vmin);
  text-align: left;
}

.App-bottom {
  font-size: calc(0.063rem + 1vmin);
  text-align: center;
  position: absolute;
  bottom: 0.625rem
}

a {
  color: #c6f7f9;
}

.transform{
  transition: 0.5s ease-in-out;
}

.transform:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}



h1{
  font-size: calc(6.25rem + 2vmin);
}

.films-watched-header {
  background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1.25rem + 2vmin);
  color: white;
}

.films-watched-body {
  font-size: calc(0.813rem + 2vmin);
  text-align: left;
}

.title{
  font-size: 2.5rem;
  line-height: 2;
}

.genre-loading {
  opacity: 1;
  -webkit-animation: fade 2s ease-in-out;
          animation: fade 2s ease-in-out;
}

@-webkit-keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}

@keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}

.map-title {
  font-size: calc(1.563rem + 1vmin);
  color: white;
  padding:0.625rem;
  height: 30%;
}

.map-header {
  padding:5.5rem;
  padding-bottom: 3rem;
}

.map-hack {
  padding-bottom: 4rem;
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);
}

.practice-map {
  position:absolute;
  height: 61.5%;
  width: 100%;
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);

}

.background {
  background-image: linear-gradient(to right, #249494 0%, #330867 100%);
  height: 100%;
  width: 100%;
}

.top-genres-header {
  background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1.25rem + 2vmin);
  color: white;
}

.bar-chart {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.fade-in-animation {
    opacity: 1;
    -webkit-animation: fade-in 0.5s ease-in;
            animation: fade-in 0.5s ease-in;
  }
  
  @-webkit-keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
  
  @keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
